* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  overflow-y: scroll;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  padding: 0 1rem;
}
header {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 10px;
}

header ul {
  display: flex;
  justify-content: center;
}

header ul li {
  list-style: none;
  margin-right: 15px;
}

header ul li:last-child {
  margin-right: 0;
}

button {
  font-size: small;
}

.btnForm {
  flex: 1 1;
  padding: 7px 20px;
  display: inline-block;
  border: none;
  background-color: #555;
  color: #fff;
  cursor: pointer;
}

.btnForm:hover {
  background-color: #666;
}

.mainForm {
  margin: 5px auto;
  display: flex;
  width: 500px;
}

header a {
  color: #fff;
  text-decoration: none;
}
.mathContainer {
  margin: 0 auto;
  width: 700px;
  text-align: center;
}

.buttons {
  display: flex;
  padding: 10px 20px;
  justify-content: space-around;
}

.progress-bar {
  -webkit-animation: width 0.7s;
          animation: width 0.7s;
}

.fade-enter {
  opacity: 0.01;
}
.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 2s ease-in;
  transition: opacity 2s ease-in;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0.01;
  -webkit-transition: opacity 2s ease-in;
  transition: opacity 2s ease-in;
}

